import Withdraw from "./withdraw-model.js";
const withdraw = new Withdraw()
export default {
	data() {
		return {
			userInfo: {},
			withdrawNum: 0
		}
	},
	onLoad() {
		const that = this
		that._onLoad()
	},
	methods: {
		_onLoad(callBack) {
			// 使用vuex获取原有的用户信息
			const that = this
			that.userInfo = that.$store.state.userInfo;
		},
		getWithdrawNum(e) {
			const that = this
			that.withdrawNum = withdraw.get_input_val(e)
			console.log(that.withdrawNum)
		},
		runWithdraw() {
			const that = this
			withdraw.runWithdraw({
				money: that.withdrawNum
			}, (res) => {
				withdraw.show_tips(res.message)
				// if (res.status_code == 'ok') {
				// 	withdraw.show_tips()
				// }
			})
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		var that = this;
		that.page = 1;
		that._onLoad(() => {
			uni.stopPullDownRefresh();
		});
	},
	//上拉加载更多
	// onReachBottom() {
	//   var that = this;
	//   if (that.last_page == that.page) {
	//     return;
	//   }
	//   that.page += 1;
	//   that.getListData();
	// },
	// 分享
	onShareAppMessage() {
		let shareData = {
			title: '',
			path: `pages/index/index?${this.userInfo.id}`,
			imageUrl: ''
		}
		return withdraw.onShareAppMessage(shareData);
	}
}
